<template lang="pug">
	li
		h2: a(:href="artist.url" ) {{ artist.name }}
		img(:src="artist.image[2]['#text']")
</template>

<script>
export default {
	name: 'artist',
	props: ['artist']
}
</script>

<style lang="stylus">
li
	display block
	margin 10px 0
a
	text-decoration none
	color blue
</style>


